<template>
  <div class="main">
    <div class="gallery-section">
      <div class="inner-width">
        <h1>My Gallery</h1>
        <div class="border"></div>
        <div class="gallery">
          <a
            href="./static/img/6.jpg"
            class="image"
          >
            <img
              src="../../static/img/6.jpg"
              @click="show"
              alt=""
            />
          </a>
          <a
            href="./static/img/5.png"
            class="image"
          >
            <img
              src="../../static/img/5.png"
              @click="show"
              alt=""
            />
          </a>
          <a
            href="./static/img/4.jpg"
            class="image"
          >
            <img
              src="../../static/img/4.jpg"
              @click="show"
              alt=""
            />
          </a>
          <a
            href="./static/img/3.png"
            class="image"
          >
            <img
              src="../../static/img/3.png"
              @click="show"
              alt=""
            />
          </a>
          <a
            href="./static/img/2.jpg"
            class="image"
          >
            <img
              src="../../static/img/2.jpg"
              @click="show"
              alt=""
            />
          </a>
          <a
            href="./static/img/1.jpg"
            class="image"
          >
            <img
              src="../../static/img/1.jpg"
              @click="show"
              alt=""
            />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// console.log($('.gallery').magnificPopup)
export default {
  name: 'FlexAlbum',
  data() {
    return {}
  },
  methods: {
    show() {
      $('.gallery').magnificPopup({
        delegate: 'a',
        type: 'image',
        gallery: {
          enabled: true
        }
      })
    }
  }
}
</script>
<style scoped>
* {
  box-sizing: border-box;
}
.main {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  z-index: 1;
}
.gallery-section {
  width: 100%;
  padding: 60px 0;
  background: #f1f1f1;
}
.inner-width {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
}
.gallery-section h1 {
  text-align: center;
  text-transform: uppercase;
  color: #333;
}
.border {
  width: 180px;
  height: 4px;
  background: #333;
  margin: 60px auto;
}
.gallery-section .gallery {
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
}
.gallery-section .image {
  flex: 25%;
  overflow: hidden;
  cursor: pointer;
}
.gallery-section .image img {
  width: 100%;
  height: 100%;
  transition: 0.4s;
}
.gallery-section .image:hover img {
  transform: scale(1.4) rotate(15deg);
}
@media screen and (max-width: 960px) {
  .gallery-section .image {
    flex: 33.33%;
  }
}
@media screen and (max-width: 768px) {
  .gallery-section .image {
    flex: 50%;
  }
}
@media screen and (max-width: 480px) {
  .gallery-section .image {
    flex: 100%;
  }
}
</style>


