<template>
  <div style="background-color:red">
    苹果：
    <input
      type="checkbox"
      value="1"
      v-model='choose["a"]'
    >
    雪梨：
    <input
      type="checkbox"
      v-model='choose["b"]'
      value="2"
    >
    橙：
    <input
      type="checkbox"
      v-model='choose["c"]'
      value="3"
    >
    <input
      type="checkbox"
      :checked="flag"
      v-model="flag"
      value="flagaaaa"
    />
    <div>复选框选择{{value}}：{{choose["a"]}}--{{choose["b"]}}--{{choose["c"]}}--{{flag}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      choose: { a: [], b: [], c: [] },
      value: '',
      aaa: true,
      flag: 0
    }
  },
  methods: {},
  created() {
    this.bus.$on('brothermsg', val => {
      this.value = val
    })
  }
}
</script>

<style scoped>
</style>



