<template>
  <div class="main">
    <canvas
      id="canvas"
      style="position:absolute;z-index:1"
    ></canvas>
    <div class="container">
      <div
        class="sidebar"
        :style="isShow?'':navStyle"
      >
        <ul>
          <li><a
              id="article"
              name="文章"
              class="iconfont icon-ai-article"
              href="#"
            ></a></li>
          <li>
            <router-link
              tag="a"
              id="picture"
              class="iconfont icon-tupian"
              href="#"
              to="/a"
            ></router-link>
          </li>
          <li><a
              id="music"
              class="iconfont icon-yinyue"
              href="#"
            ></a></li>
        </ul>
      </div>
      <a
        :class="['btn',isShow?'iconfont icon-guanbi':'iconfont icon-zhankai']"
        @click="changeShow"
      ></a>
      <div id="landlord">
        <div
          class="message"
          style="opacity:0"
        ></div>
        <canvas
          id="live2d"
          width="280"
          height="250"
          class="live2d"
        ></canvas>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
  
<script>
import '../static/live2d/js/live2d.js'
import '../static/live2d/js/message.js'
export default {
  data() {
    return {
      isShow: true,
      navStyle: {
        marginLeft: '-60px'
      }
    }
  },
  components: {},
  created() {},
  mounted() {
    loadlive2d('live2d', './static/live2d/model/xuexiaoban/model.json')
    new Weather({
      id: 'canvas',
      weatherType: 'snow',
      num: 100
    })
  },
  methods: {
    changeShow() {
      this.isShow = !this.isShow
    }
  }
}
</script>

<style lang=''>
.main {
  height: 100%;
  background: url(../static/img/background.jpg) no-repeat;
  overflow: hidden;
}
.container {
  position: fixed;
  display: flex;
  align-items: center;
  height: 100%;
  z-index: 1000;
}
.sidebar {
  width: 50px;
  height: 210px;
  background: #2c3e50;
  overflow: hidden;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  transition: all 0.8s;
}
.sidebar ul {
  margin: 0;
  padding: 0;
}

.sidebar ul li {
  list-style: none;
}

.sidebar ul li a {
  text-decoration: none;
  color: white;
  height: 70px;
  width: 100%;
  font-size: 30px;
  line-height: 70px;
  text-align: center;
  display: block;
  transition: all 0.6s;
}

.sidebar ul li a:hover {
  background: #e84118;
}
.btn {
  float: left;
  padding: 0 5px;
  text-decoration: none;
  color: #2c3e50;
  font-size: 30px;
  cursor: pointer;
}
</style>


