import Vue from 'vue'
import App from './App'
import router from './router'
import '@/font/iconfont.css'
import 'magnific-popup/dist/magnific-popup.css'
import $ from 'jquery'
import 'magnific-popup/dist/jquery.magnific-popup.js'
import '@/js/weatherDrop.js'

Vue.prototype.bus = new Vue()
